.control-group {
  display: flex;
  flex: 0 0 auto;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: stretch;
  padding: 5px 15px;
  /***
   * components
   ***/
}
.control-group > * {
  padding-right: 15px;
  flex: 0 0 auto;
}
.control-group > *:last-child {
  padding-right: 0;
}
.control-group .button-group,
.control-group .inline-form .input-group {
  margin-bottom: 0px;
  display: flex;
}
.control-group .button-group > *,
.control-group .inline-form .input-group > * {
  border-radius: 0;
}
.control-group .button-group > :first-child,
.control-group .inline-form .input-group > :first-child {
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}
.control-group .button-group > :last-child,
.control-group .inline-form .input-group > :last-child {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
.control-group .inline-form {
  flex: 0 0 auto;
  flex-direction: column;
  display: flex;
}
.control-group .inline-form > * {
  flex-shrink: 0;
}
.control-group .inline-form > .typeahead {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
}
.control-group .inline-form > .typeahead > * {
  flex-shrink: 0;
}
.control-group .inline-form > .typeahead > .input-group {
  display: flex;
  flex: 1 0 auto;
}
.control-group .inline-form > .typeahead > .input-group > * {
  float: none;
  height: auto;
  width: auto;
  flex: 0 0 auto;
}
.control-group .inline-form > .typeahead > .input-group input[type="text"] {
  flex: 1 1 100%;
}
.control-group > .fill {
  flex: 1 1 1%;
}
.nav-controls .column {
  flex-direction: column;
}



