}
navbar > * {
  padding-right: 10px;
}
navbar .navbar-text {
  margin-top: 6px;
  margin-bottom: 6px;
}
navbar > .name {
  align-self: center;
  font-size: 18px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
navbar button {
  color: #5A5A5A;
  background-color: transparent;
}
navbar button:hover {
  color: #5A5A5A;
  background-color: transparent;
}
navbar button:focus {
  color: #5A5A5A;
  background-color: transparent;
}
navbar button:active,
navbar button.active {
  color: #2D2D2D;
  background-color: transparent;
  box-shadow: none;
}
navbar button:active:focus,
navbar button.active:focus {
  outline: none;
}
navbar button[disabled] {
