.truncate-by-height {
  position: relative;
  overflow: hidden;
}
.truncate-by-height:before {
  content: " ";
  width: 100%;
  height: 15px;
  position: absolute;
  left: 0;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.01) 1%, rgba(255, 255, 255, 0.99) 99%, #ffffff 100%);
}



