.chart-top-title {
  flex: 0;
  text-align: center;
  font-weight: bold;
  font-size: 11px;
}
.chart-canvas {
  min-width: 100%;
  flex: 1;
}
.tab-dashboard.theme-dark .ngLegendValue {
  color: #aaaaaa;
}
.tab-dashboard.theme-dark .flot-tick-label {
  color: #aaaaaa;
}
.suggestions {
  left: 0;
  width: 100% !important;
  background-color: #ffffff;
  color: #2D2D2D;
  border: 1px solid #ecf0f1;
  border-top: 2px solid #ecf0f1;
  border-radius: 0px 0px 4px 4px !important;
  z-index: 10000;
  max-height: 378px;
  overflow-y: auto;
}
.suggestions .suggestion {
  border-bottom: 1px solid #ecf0f1;
  padding: 5px 20px;
}
.suggestions .suggestion:hover {
  background-color: #ecf0f1;
}
.suggestions .suggestion.active {
  background-color: #ecf0f1;
}
.suggestions .suggestion-details {
  background-color: #ffffff;
  padding: 10px;
  border-radius: 4px;
}
.suggestions .suggestion-details > table {
  color: #2D2D2D;
  margin-bottom: 0px;
}
.timelion-vis {
  min-width: 100%;
  display: flex;
  flex-direction: column;
}
.timelion-vis [chart] {
  min-width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
}
.timelion-vis-editor-expression {
  position: relative;
}
.timelion-vis-editor-expression .suggestions {
  position: absolute;
}



